// ==========================================================================
// Modifiers
// ==========================================================================

.icon--xs { // Extra small
  font-size: 0.625rem;
}

.icon--sm { // Small
  font-size: 0.75rem;
}

.icon--ms { // Medium small
  font-size: 1.2em;
}

.icon--md { // Medium
  font-size: 1.5rem;
}

.icon--lg { // Large
  font-size: 2.125rem;
}

.icon--xl { // Extra Large
  font-size: 2.75rem;
}

.icon--hg { // Huge
  font-size: 5rem;
}

.icon--gi { // Gigantic
  font-size: 8.75rem;
}

.icon--circle {
  border-radius: 50%;
  color: white;
  display: inline-block;
  padding: 0.5em;
}

.icon--outline {
  border: 2px solid;
  border-radius: 50%;
  font-weight: 600;
  padding: 0.3em;
}

.icon-do-not {
  color: $error;
  font-size: 1.8rem;
  position: relative;
  z-index: 5;

  &::before {
    content: "\e99c";
  }

  > [class^="icon-"],
  [class*=" icon-"] {
    color: $font-color;
    float: left;
    font-size: 1.125rem;
    left: 0;
    margin: 0.3em;
    position: absolute;
    top: 0;
    z-index: -1;
  }

  &.icon--xs {
    font-size: 1rem;

    > [class^="icon-"],
    [class*=" icon-"] {
      font-size: 0.625rem;
    }
  }

  &.icon--sm {
    font-size: 1.8rem;

    > [class^="icon-"],
    [class*=" icon-"] {
      font-size: 1.125rem;
    }
  }

  &.icon--md {
    font-size: 2.4rem;

    > [class^="icon-"],
    [class*=" icon-"] {
      font-size: 1.2rem;
      margin: 10px;
    }
  }

  &.icon--lg {
    font-size: 3.4rem;

    > [class^="icon-"],
    [class*=" icon-"] {
      font-size: 2.125rem;
    }
  }

  &.icon--xl {
    font-size: 4.4rem;

    > [class^="icon-"],
    [class*=" icon-"] {
      font-size: 2.75rem;
    }
  }
}
